import React from 'react';
import {Link} from 'react-router-dom';

function Movies({year, title, summary, poster, genres}) {
    return (
        <Link to={{
            pathname: '/detail',
            state: {year, title, summary, poster, genres} // year: year 이런식으로 써야하는데 동일할땐 한번만
        }} className='detailLnk'>
            <div className="movies_list">
                <img src={poster} alt={title} />
                <div>
                    <ul className="movies_genres">
                        {
                            genres.map(
                                (data, index) => (<li key={index}>{data}</li>)
                            )
                        }
                    </ul>
                    <h3 className="movies_title">{title}</h3>
                    <h5 className="movies_year">{year}</h5>
                    <p className="movies_summary">{summary}</p>
                </div>
            </div>
        </Link>
    );
}

export default Movies;