import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import Loading from './Loading';
import Movies from './Movies';
import './App.scss'
import './Section.scss'



class Section extends Component {
    state = {
        kim : [],
        isLoading : true
    }
    getMovies = async () => {
        const {data: {data: {movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json")
        this.setState({
            kim : movies
        })
    }

    //render() 수행 후 호출되는 함수 componentDidMount()
    componentDidMount(){
        this.getMovies()
        setTimeout(() => {
            this.setState({
                isLoading:false
            })
        }, 1500)
    }

    render() {
        const { kim, isLoading } = this.state;
        return (
            <>
            {
                isLoading ? <Loading /> : (
                    <section className="container">
                        <h1>Movie List</h1>
                        <div className="movies">
                            {
                                kim.map((movies) => (
                                    <Movies key={movies.id} year={movies.year} title={movies.title} summary={movies.summary} poster={movies.medium_cover_image} genres={movies.genres} />
                                ))
                            }
                        </div>
                    </section>
                )
            }
            </>
        );
    }
}

export default Section;