import React, { Component } from 'react';
import Header from '../components/Header'
import Section from '../components/Section';
import Footer from '../components/Footer';
import './Home.scss';

class Home extends Component {

  render() {
    return (
        <div className="wrap">
            <Header />
            <Section />
            <Footer />
        </div>
    );
  }
}

export default Home;